



















































































































import Vue from 'vue'
import { dataGridHeaders, desserts } from '../example-data'

const sandboxTemplate =
`<v-data-grid-select
 :autocomplete = "autocomplete"
 :chips = "chips"
 :dense = "dense"
 :multiple = "multiple"
 :items="items"
 :clearable = "clearable"
 :headers="dataGridHeaders"
 item-key = "name"
 item-text = "name"
 :dark = "dark"
          >
          <template v-if="customSlots" v-slot:headerTable.fat="{ header }">
            <v-icon color ="red">mdi-cake</v-icon>
          </template>
          <template v-if="customSlots" v-slot:itemTable.calories="{ item }">
            <v-chip :color="getColor(item.calories)" dark>{{ item.calories }}</v-chip>
          </template>
          <template v-if="customSlots" v-slot:itemTable.fat="{ item }">
            <v-chip :color="getColorFat(item.fat)" dark>{{ item.fat }}</v-chip>
          </template>
</v-data-grid-select>`

const sandboxCode =
`export default ({
  data: () => ({
    items: desserts, // see items
    dataGridHeaders: dataGridHeaders, // see headers
    chips: false,
    multiple: false,
    dense: false,
    clearable: false,
    dark: false,
    autocomplete: false,
    customSlots: false,
  }),
  methods: {
    getColor (calories) {
      if (calories > 400) return "red"
      else if (calories > 200) return "orange"
      else return "green"
    },
    getColorFat (fat: any) {
      if (fat > 20) return "red"
      else if (fat > 10) return "orange"
      else return "green"
    }
  }
})`

export default Vue.extend({
  data: () => ({
    items: desserts,
    dataGridHeaders: dataGridHeaders,
    desserts: desserts,
    //
    chips: false,
    multiple: false,
    dense: false,
    clearable: false,
    dark: false,
    autocomplete: false,
    customSlots: false,
    deletableChips: false,
    smallChips: false,
    selectedItems: [],
    useToolbar: false,
    //
    codeSandbox: false,
    sandboxTemplate: sandboxTemplate,
    sandboxCode: sandboxCode
  }),
  methods: {
    getColor (calories: any) {
      if (calories > 400) return 'red'
      else if (calories > 200) return 'orange'
      else return 'green'
    },
    getColorFat (fat: any) {
      if (fat > 20) return 'red'
      else if (fat > 10) return 'orange'
      else return 'green'
    }
  }
})
